export default{
	data(){
		const nowDate = new Date();
		let hour = nowDate.getHours();
		let minute = nowDate.getMinutes();
		const hours = []
		const minutes = []
		for(let i = 0;i <= 23;i++){
			i = i > 9 ? i : '0' + i
			hours.push(i)
		}
		for(let i = 0;i <= 59;i++){
			i = i > 9 ? i : '0' + i
			minutes.push(i)
		}
		return{
			indicatorStyle:`background-image:linear-gradient(#65A3E0, #0657A6, #65A3E0);z-index:-1;`,
			maskStyle:``,
			hour,
			hours,
			minutes,
			minute,
			num:1,
			order_time:false,
			monthAay:"",
			year:"",
			time:[0,0],
			value:[hour,minute],
			dayList:[
				{
					day:1,
				},{
					day:2,
				},{
					day:3,
				},{
					day:4,
				},{
					day:5,
				}
			],
			
		}
	},
	created() {
		this.order_time = true
	},
	filters:{
		filterWeek1(str){
			if(str){
				return "今天"
			}
		},
		filterWeek2(str){
			if(str){
				return "明天"
			}
		}
	},
	methods:{
		handleDay(e){
			this.num = e.currentTarget.dataset.num;
			
		},
		changrTimer(e){
			const val = e.detail.value;
			this.hour = this.hours[val[0]];
			this.minute = this.minutes[val[1]];
		},
		getEvery(day){
			let toDay = new Date();
			let weekDayArr=["星期日","星期一","星期二","星期三","星期四","星期五","星期六"];
			toDay.setTime(toDay.getTime() + day*24*60*60*1000)
			this.year = toDay.getFullYear()
			let monthAay = (toDay.getMonth()+1) + "月" + toDay.getDate() + "日";
			let week = weekDayArr[toDay.getDay()]; 
			
			return {
				monthAay,
				week
			}
		},
		// 关闭
		handleClose(){
			this.$emit("handleClose",false)
		},
		// 约定时间
		handleSure(){
			this.monthAay = this.getEvery(this.num-1).monthAay;
			let reg = /\d+/g;
			let months = this.monthAay.match(reg).join("-");
			let date = this.year+"-"+months+" "+this.hour+":"+this.minute+":"+"00";
			if(new Date(date) <= new Date()){
				this.$c.msg("您选择的时间已过")
			}else{
				this.$emit("handleDate",date)
			}
		}
		
	}
}
